import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
import MenuItem from '@mui/material/MenuItem';
import AdbIcon from '@mui/icons-material/Adb';
import CallIcon from '@mui/icons-material/Call';
import ReactDOM from "react-dom";


import React from "react";
import "./Nav.css";
import Landingpage from '../landingpage/Landingpage';
import About from '../landingpage/about/about';
import Mywork from '../landingpage/mywork/Mywork';
import Skill from '../landingpage/skills/skill';
import { ContactUs } from '../landingpage/getintouch/getintouch';

const pages = ['Home', 'About','My Work','Contact Me', 'My Resume'];
const settings = ['+254745397463'];

const Navbar = () => {

  const [anchorElNav, setAnchorElNav] = React.useState(null);
  const [anchorElUser, setAnchorElUser] = React.useState(null);

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };
  const handleOpenUserMenu = (event) => {
    setAnchorElUser(event.currentTarget);
  };
  const handleCall = () =>{
    window.open('tel:+254745397463', '_blank');

  };

  const handleCloseNavMenu = (page) => {
    switch(page) {
        case "My Resume":
              window.open('https://davidlangat.me/my-resume', '_blank');
        break;
        case "Home":
            ReactDOM.render(
                <React.StrictMode>
                    <Landingpage />
                </React.StrictMode>,
                document.getElementById("main")
              );
        break;
        case "Contact Me":
            ReactDOM.render(
                <React.StrictMode>
                    <ContactUs />
                </React.StrictMode>,
                document.getElementById("main")
              );
        break;
        case "About":
            ReactDOM.render(
                <React.StrictMode>
                    <About />
                    <Skill />
                </React.StrictMode>,
                document.getElementById("main")
              );
        break;
        case "My Work":
            ReactDOM.render(
                <React.StrictMode>
                    <Mywork />
                </React.StrictMode>,
                document.getElementById("main")
              );
        break;
        default:
          // code block
      }
    setAnchorElNav(null);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  return (
    <AppBar position="static" style={{ background: 'transparent', boxShadow: 'none'}}>
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <AdbIcon sx={{ display: { xs: 'none', md: 'flex' }, mr: 1 }} />
          <Typography
            variant="h4"
            noWrap
            component="a"
            href="/"
            sx={{
              mr: 2,
              display: { xs: 'none', md: 'flex' },
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: '.3rem',
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            David Langat
          </Typography>

          <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              color="inherit"
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: 'block', md: 'none' },
              }}
            >
              {pages.map((page) => (
                <MenuItem 
                key={page} 
                onClick={() => handleCloseNavMenu(page)}>
                  <Typography 
                  sx={{
                    fontWeight: 700,
                     fontsize:20
                     }}
                 textAlign="center" >
                    {page}
                </Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>
          <AdbIcon sx={{ display: { xs: 'flex', md: 'none' }, mr: 1 }} />
          <Typography
            variant="h5"
            noWrap
            component="a"
            href=""
            sx={{
              mr: 2,
              display: { xs: 'flex', md: 'none' },
              flexGrow: 1,
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: '.3rem',
              color: 'inherit',
              textDecoration: 'none',
            }}
          >
            Dave
          </Typography>
          <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
            {pages.map((page) => (
              <Button
                variant='h1'
                className='myNav'
                key={page}
                onClick={() => handleCloseNavMenu(page)}
                sx={{ 
                    my: 2, 
                    color: 'white', 
                    display: 'block',
                    fontWeight: 900,
                    fontsize:30,
                    ml:2
                     }}
              >
                {page}
              </Button>
            ))}
          </Box>

          <Box sx={{ flexGrow: 0 }}>
            <Tooltip title="" onClick={handleCall} >
              <IconButton onMouseOver={handleOpenUserMenu} sx={{ p: 0 }}>
                <CallIcon/>
              </IconButton>
            </Tooltip>
            <Menu
              sx={{ mt: '45px' }}
              id="menu-appbar"
              anchorEl={anchorElUser}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              open={Boolean(anchorElUser)}
              onClose={handleCloseUserMenu}
            >
              {settings.map((setting) => (
                <MenuItem key={setting} onClick={handleCall} >
                  <Typography textAlign="center">{setting}</Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
};
export default Navbar;
