import React, { useRef } from 'react';
import emailjs from '@emailjs/browser';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';


export const ContactUs = () => {
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_2gootzc', 'template_r20wree', form.current, 'G52PkheZ2xeUmWOLA')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
      e.target.reset();
  };
  const theme = createTheme();


  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
         
          <Typography component="h1" variant="h3" sx={{color:"white"}}>
           Your Message
          </Typography>
          <Box component="form" ref={form} onSubmit={sendEmail} noValidate sx={{ mt: 1}}>
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label="Your Name"
              name="from_name"
              autoFocus
              sx={{ input: { color: 'black' }, backgroundColor:"white" }}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="from_email"
              label="Your Email"
              type="email"
              sx={{ input: { color: 'black' }, backgroundColor:"white" }}
            />
             <TextField
              margin="normal"
              required
              fullWidth
              name="message"
              label="Your Message"
              type="message"
              sx={{ input: { color: 'black' }, backgroundColor:"white" }}
            />
            
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
                Send
            </Button>
            
          </Box>
        </Box>
      </Container>
    </ThemeProvider>
  );
};